var slideTimer,chageTimer
var slideTime = 3000
export default {
	props:{
		images: {
			type: Array,
			default: () => []
		}
	},
	data() {
		return {
			showImg: null, // 添加首尾两张图片，方便循环
			slideIndex: 0, // 轮播图偏移下标
			tempIndex: 0,
			imgAnimation: true, // 是否展示动画
			imgWidth: 0, // 图片宽度
			startX: null, // 手指触碰的起始X坐标
		}
	},
	watch:{
		slideIndex: 'indexChange'
	},
	computed:{
		/*
			name: 轮播图点下标
			description: 获取轮播图点的下标
			watch: slideIndex
			return: Number
		*/
		dotIndex(){
			if(this.slideIndex === -1)
			{
				return this.images.length - 1
			}
			else if(this.slideIndex === this.images.length)
			{
				return 0
			}
			return this.slideIndex
		}
	},
	created() {
		this.showImg = [
			this.images[this.images.length-1],
			...this.images,
			this.images[0]
		]
		this.setTimer()
	},
	mounted() {
		/* 获取图片的宽度 */
		uni.createSelectorQuery().in(this).select('.content').boundingClientRect(result => {
			if (result)
			{
				this.imgWidth = result.width
			}
			else
			{
				this.imgWidth = 325
			}
		}).exec()
	},
	methods: {
		/*
			name: 配置循环时钟
			description: 开启循环时钟
			input: null
			return : null
		*/
		setTimer(){
			slideTimer = setInterval(() => {
				this.imgAnimation = true
				this.slideIndex++
			},slideTime)
		},
		/*
			name: 轮播图下标变化
			description: 监听轮播图下标变化，处理首尾图循环
			input: 当前轮播图下标
			return : null
		*/
		indexChange(index){
			/* 滑动过程不执行 */
			if(this.startX !== null)
			{
				return
			}
			/* 延迟切换轮播图下标 */
			const setIndex = (i) => {
				chageTimer = setTimeout(() => {
					this.imgAnimation = false
					this.slideIndex = i
					setTimeout(() => {
						this.imgAnimation = true
					})
				},this.imgAnimation ? 1000 : 0)
			}
			if(index === this.images.length)
			{
				setIndex(0)
			}
			else if(index === -1)
			{
				setIndex(this.images.length-1)
			}
		},
		/*
			name: 手指触碰开始
			description: 监听手指按下事件，断开轮播计时
			input: 系统参数
			return: null
		*/
	 touchstart(e){
		 clearInterval(slideTimer)
		 clearTimeout(chageTimer)
		 /* 停止切换动画*/
		 this.imgAnimation = false
		 /* 记录起始坐标*/
		 this.startX = e.changedTouches[0].pageX
	 },
	 /*
	 	name: 手指触碰结束
	 	description: 监听手指离开事件，开始轮播计时
	 	input: null
	 	return: null
	 */
	 touchend(){
		 /* 清空坐标标记，打开滑动记录*/
		 this.startX = null
		 this.slideIndex = Math.round(this.slideIndex)
		 this.setTimer()
	 },
	 /*
	 	name: 手指移动
	 	description: 监听手指移动事件，改变图片偏移值
	 	input: 系统参数
	 	return: null
	 */
	 touchmove(e){
		 const touchX = e.changedTouches[0].pageX - this.startX
		 const rate = touchX / this.imgWidth
		 this.slideIndex -= rate
		 this.startX = e.changedTouches[0].pageX
	 }
	},

}
